export default {
	data() {
		return {};
	},
	props: {
		shopList: {
			type: Array,
			default: () => []
		},
		loginMsg: {
			type: Object,
			default: () => {}
		}
	},
	components: {},
	methods: {
		goLogin() {
			if (this.loginMsg === null) {
				uni.switchTab({
					url: "/pages/my/my"
				})
			} else {
				uni.switchTab({
					url: "/pages/index/index"
				})
			}
		}
	},
	mounted() {},
	watch: {},
	computed: {}
};
